*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
html,body{
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
body{
  background:#ecf0f1;
}
.fork{
  position: absolute;
  left: 0px;
  top: 0px;
}
.gal{
  display: flex;
  justify-content: center;
  img{
    height: 100px;
    margin: 4px;
    cursor: pointer;
    &:hover{
      transform: scale(1.01);
      box-shadow: 1px 1px 5px 0px #999;
    }
  }
}
.title{
  text-align: center;
  font-size: 1.2em;
  margin-bottom: 1em;
  padding: 0px 3em;
}
.single-opener-holder{
  text-align: center;
  padding: 1em;
}
.single-example{
  cursor: pointer;
  display: inline-block;
  padding: 1em 1.6em;
  background-color: #16a085;
  color: #fff;
  margin:auto;
  border-radius: 3px;
  box-shadow: 1px 1px 4px 0px #888;
  margin-bottom: 1em;
  &:hover{
    box-shadow: 1px 1px 7px 0px #666;
  }
}
@media (max-width:500px){
  .gal{
    flex-wrap: wrap;
  }
}